import React, { useState, useEffect } from 'react'
import Navigation from './Navigation'
import unsplash01 from "../images/unsplash01.jpg";
import unsplash02 from "../images/unsplash02.jpg";
import unsplash03 from "../images/unsplash03.jpg";


function Header() {

  const [ imgIndex, setImgIndex] = useState(0);


  let currentindex= 0;

  useEffect(() => {
    const interval = setInterval(() => {
      currentindex += 1;
      if(currentindex > 2){
        currentindex = 0;
      }
      console.log("current"+currentindex)
        setImgIndex(currentindex);
      
    }, 5000);
    return () => clearInterval(interval)
  }, [])

  const imagelist = [ unsplash01, unsplash02, unsplash03 ];

  return (
    <div className="header">
     <Navigation />
     <div className="head-wrapper" >
      <div className="head1 head">
        <img src={imagelist[imgIndex]} alt="unsplash"/>
        <div className="text">
          <h2> superior knwoledge does not grow on trees</h2>
          <h2>dont believe it can be done</h2>
          <h2>success start from who you hire</h2>
          <button>learn more</button>
        </div>
        
      </div>
     </div>
    </div>
  )
}

export default Header
